module.exports = ":root {\n    /* galaxy image from https://unsplash.com/s/photos/galaxy */\n    --bg-image: url('https://images.unsplash.com/photo-1538370965046-79c0d6907d47?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1169&q=80');\n  }\n  \n  body {\n    background-color: #1D1E22;\n    font-family: sans-serif;\n    display: flex;\n  }\n  \n  .card {\n    margin: 1rem;\n    padding: 1rem;\n    height: 300px;\n    width: 300px;\n    text-align: center;\n    color: white;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    position: relative;\n    background-color: gray;\n    border-radius: 10px;\n  }\n  \n  .bg-blur {\n    overflow: hidden;\n    background-color: transparent;\n  }\n  \n  .bg-blur::before {\n    content: '';\n    background-image: var(--bg-image);\n    background-size: cover;\n    height: 100%;\n    width: 100%;\n    position: absolute;\n    -webkit-filter: blur(30px);\n            filter: blur(30px);\n    z-index: -1;\n  }"